/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2013-02-21-delete-button-back-button/index.md absPath of file >>> MarkdownRemark","html":"<p>I'm not sure who decided that the delete button would make a great back button in a web browser but they need to have their keyboard license revoked. </p>\n<p>After filling out a lengthy online form today I messed up some text and thinking that the text field had focus I pressed delete since that's how you delete text. The text field didn't have focus, the browser went back a screen an everything I had entered was lost to digital oblivion.</p>\n<p>After finishing a lengthy list of adjectives for the guy who made this UI choice I decided to take control of my life/browser and now my delete button does what it is supposed to - delete stuff.</p>\n<p>Should you feel the same here's a five step process for preserving your sanity and the innocence of any ears within twenty feet:</p>\n<ol>\n<li>Open a new Firefox window</li>\n<li>Enter about:config into the location bar. Press Enter. The about:config page will open. It's a huge list of Firefox configuration properties.</li>\n<li>Enter browser.backspace into the filter. This will bring the 'Browser.backspace_action' preference into view.</li>\n<li>Contrl-click the 'Browser.backspace_action' Value column, and choose Modify from the pop-up menu, then enter a new value of 2.</li>\n<li>Press OK.</li>\n</ol>\n<p><a href=\"http://hints.macworld.com/article.php?story=20070511123925218\">Extracted from a forum post on MacOS Hints.</a></p>","frontmatter":{"title":"Delete button != Back button","date":"February 21, 2013","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{}}

/***/ })

